/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import { Navbar, Nav, Form, FormControl, Container, Col, Row, Dropdown } from 'react-bootstrap'

const Layout = ({ children }) => {
  const pages = useStaticQuery(graphql`
  query AllSitePages {
    allSitePage{
        edges {
            node {
                path
            }
        }
    }
}
`)

  const sideBarItems = []

  const url = typeof window !== 'undefined' ? window.location.href : '';
  const path = url.split("/").slice(-1)[0]; // this gets the current location

  pages.allSitePage.edges.forEach(item => {
    let name = item.node.path.split("/")[1];
    if (name !== "dev-404-page" && name !== "404" && name !== "404.html" && name.length > 0) {
      sideBarItems.push(item.node.path.split("/")[1])
    }
  });


  return (
    <>
      <Container fluid className="h-100 d-flex flex-column p-0">
        <Navbar style={{ backgroundColor: "purple" }} variant="dark" expand="md" sticky="top">
          <Navbar.Brand href="#home">OnElement Documentations</Navbar.Brand>
          <Navbar.Collapse>
            <Nav className="mr-auto">
              <Nav.Link href="http://onelement.cc">Home</Nav.Link>
              <Nav.Link href="/onElementDocsWeb/getting-started">Getting Started</Nav.Link>
              <Nav.Link href="/onElementDocsWeb/about">About</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Row className="flex-fill m-0" style={{minHeight:0}}>
          <Col
            xs={12}
            sm={3}
            md={2}
            className=" border-right mh-100"
            style={{ backgroundColor: "#f8f9fa", overflowY:"scroll"}}
          >
            <Form >
              <FormControl type="text" placeholder="Search" className="mt-2 mb-2"/>
            </Form>
            <Dropdown.Divider />
            <Navbar bg="light" variant="light" expand="sm" className="p-0">
              <Navbar.Toggle aria-controls="sideBar-nav" />
              <Navbar.Collapse id="sideBar-nav">
                <Nav className="flex-column mr-auto">
                  {sideBarItems.map(item => (
                    <Nav.Link 
                      key={item} 
                      href={`/onElementDocsWeb/${item}`} 
                      active={(path === item) ? true : false}
                      className="font-weight-bold"
                      >
                        {item[0].toUpperCase() + item.slice(1).replace("-", " ")}
                    </Nav.Link>
                  ))}
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </Col>
          <Col
            xs={12}
            sm={9}
            md={10}
            className="mh-100 p-0"
            style={{overflowY:"scroll"}}>
            {children}
          </Col>
        </Row>
      </Container>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
